import { createStore } from 'vuex'

export default createStore({
  state: {
    user: {
      username: '',
      role: '' // 'admin' | 'user'
    },
    workOrders: [
      {
        id: '001',
        project: 'Road Project A',
        overtime: true,
        hours: 3.5,
        created_at: '2024-04-10 10:30'
      },
      {
        id: '002',
        project: 'Bridge Maintenance B',
        overtime: false,
        hours: 2,
        created_at: '2024-04-09 13:00'
      },
      {
        id: '003',
        project: 'Pipeline Fix C',
        overtime: true,
        hours: 4.5,
        created_at: '2024-04-08 08:00'
      },
      {
        id: '004',
        project: 'Bridge Maintenance B',
        overtime: true,
        hours: 3,
        created_at: '2024-04-07 16:45'
      },
      {
        id: '005',
        project: 'Tunnel Cleaning D',
        overtime: false,
        hours: 8.1,
        created_at: '2024-04-03 11:43'
      }
    ]
  },
  getters: {
    isLoggedIn(state) {
      return !!state.user && !!state.user.role
    },
    isAdmin(state) {
      return state.user.role === 'admin'
    },
    workOrders(state) {
      return state.workOrders
    },
    hoursByProject(state) {
      const projectToHours = {}
      state.workOrders.forEach(order => {
        const key = order.project
        if (!projectToHours[key]) {
          projectToHours[key] = 0
        }
        projectToHours[key] += Number(order.hours) || 0
      })
      return projectToHours
    }
  },
  mutations: {
    setUser(state, payload) {
      state.user.username = payload.username
      state.user.role = payload.role
    },
    logout(state) {
      state.user.username = ''
      state.user.role = ''
    },
    deleteWorkOrder(state, id) {
      state.workOrders = state.workOrders.filter(item => item.id !== id)
    }
  },
  actions: {
    login({ commit }, { username }) {
      const role = username === 'admin' ? 'admin' : 'user'
      commit('setUser', { username, role })
    }
  }
})
