<template>
  <div class="login-container">
    <a-card title="Login" class="login-card">
      <a-form :model="formState" layout="vertical" @finish="onFinish" @submit.prevent>
        <a-form-item label="Username" name="username" :rules="[{ required: true, message: '请输入用户名' }]">
          <a-input v-model:value="formState.username" placeholder="username (admin or others)" />
        </a-form-item>
        <a-form-item label="Password" name="password" :rules="[{ required: true, message: '请输入密码' }]">
          <a-input-password v-model:value="formState.password" placeholder="password" />
        </a-form-item>
        <a-form-item>
          <a-button type="primary" html-type="submit" block>Login</a-button>
        </a-form-item>
      </a-form>
    </a-card>
  </div>
</template>

<script setup>
import { reactive } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

const store = useStore()
const router = useRouter()
const formState = reactive({ username: '', password: '' })

const onFinish = () => { 
  store.dispatch('login', { username: formState.username })  
  router.push({ name: 'dashboard' })
}
</script>

<style lang="less" scoped>
.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f5f5f5;
}
.login-card {
  width: 360px;
}
</style>


